body {
  padding: 50px;
  background-color: #e79cd4;
  font-family: sans-serif;
}
#root {
  display: flex;
  justify-content: center;
}
.todoListMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165px;
}
.todoListMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: #a50b7e;
  color: #FFF;
  border: 2px solid #a50b7e;
}
.todoListMain .header button:hover {
  background-color: #a50b7e;
  border: 2px solid #a50b7e;
  cursor: pointer;
}

.todoListMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  list-style: none;
}
ul.theList {
  padding: 0;
}

 
.todoListMain .theList li:hover {
  background-color: rgb(200, 248, 136);
  cursor: pointer;
}